@use 'utility';


@mixin custom-property {
	// un-ordered, definition
	--nc-list-marker-color : #777;
	--nc-list-min-width    : 36rem;
	--nc-list-indent-width : 3rem;
	--nc-list-indent-shrink: 1.5rem;

	// definition
	--nc-list-indent-definition-offset: 1.5rem;

	// margin
	--nc-list-item-margin: 0.75rem;
}

@mixin un-ordered {
	padding-left: utility.bp(
		calc(var(--nc-list-indent-width) - var(--nc-list-indent-shrink)),
		var(--nc-list-indent-width),
		var(--nc-list-min-width),
	);
	> li {
		&::marker {
			color: var(--nc-list-marker-color);
			font-weight: bold;
		}

		// Margin
		> :is(ul, ol, dl) {  // For nesting lists
			margin-top: var(--nc-list-item-margin);
		}
	}

	// Margin
	> li + li { margin-top: var(--nc-list-item-margin); }
	&[data-nc-compact] > li + li { margin-top: 0; }
}


// -----------------------------------------------------------------------------


@mixin definition {
	padding-left: utility.bp(
		calc(var(--nc-list-indent-width) - var(--nc-list-indent-definition-offset) - var(--nc-list-indent-shrink)),
		calc(var(--nc-list-indent-width) - var(--nc-list-indent-definition-offset)),
		var(--nc-list-min-width),
	);
	> dt {
		color: var(--nc-list-marker-color);
		font-weight: bold;
	}
	> dd {
		margin-left: var(--nc-list-indent-definition-offset);

		// Margin
		> :is(ul, ol, dl) {  // For nesting Lists
			margin-top: var(--nc-list-item-margin);
		}
	}

	// Margin
	> * + dt { margin-top: var(--nc-list-item-margin); }
	&[data-nc-compact] > * + dt { margin-top: 0; }
}


// -----------------------------------------------------------------------------


@mixin custom-property-customized {
	--nc-list-marker-size: 1em;
}

@mixin customized {
	list-style: none;

	> li {
		position: relative;

		&::before {
			content : '';
			position: absolute;
			left    : -1.5rem;
			width   : var(--nc-list-marker-size);
			height  : var(--nc-list-marker-size);

			background-size: contain;
		}
	}
}
